function Loading() {
  return <p className="message">Loading...</p>;
}

function FactList({ facts, setFact, currentCategory }) {
  const factLength = facts.length;
  const currCat = currentCategory.toUpperCase();

  return (
    <section>
      <ul className="fact-list">
        {facts.map((fact) => (
          <Fact key={fact.id} fact={fact} setFact={setFact} />
        ))}
      </ul>
      <p>
        {factLength > 1
          ? "There are " +
            [factLength] +
            " facts present in the database for " +
            [currCat] +
            " category. Add your own!"
          : "There is " +
            [factLength] +
            " fact present in the database for " +
            [currCat] +
            " category. Add your own!"}
      </p>
    </section>
  );
}

function Fact({ fact, setFact }) {
  const isDisputed =
    fact.votesInteresting + fact.votesMindblowing < fact.votesFalse;
  async function handleVotes(columnName) {
    const { data: updatedFact, error } = await supabase
      .from("facts")
      .update({ [columnName]: fact[columnName] + 1 })
      .eq("id", fact.id)
      .select();

    if (!error)
      setFact((facts) =>
        facts.map((f) => (f.id === fact.id ? updatedFact[0] : f))
      );
  }

  return (
    <li className="fact">
      <p>
        {isDisputed ? <span className="disputed">[ DISPUTED]</span> : null}
        {fact.text}
        <a className="source" href={fact.source} target="_blank">
          (Source)
        </a>
      </p>
      <span
        className="tag"
        style={{
          backgroundColor: CATEGORIES.find((cat) => cat.name === fact.category)
            .color,
        }}
      >
        {fact.category}
      </span>
      <div className="vote-buttons">
        <button onClick={() => handleVotes("votesInteresting")}>
          {fact.votesInteresting}
        </button>
        <button onClick={() => handleVotes("votesMindblowing")}>
          {fact.votesMindblowing}
        </button>
        <button onClick={() => handleVotes("votesFalse")}>
          {fact.votesFalse}
        </button>
      </div>
    </li>
  );
}

export default App;
